import React from "react";
import "./Header.css";
import redberry from "../imgs/logos/redberry.png";

const Header = ({page}) => {
  return (
    <div className="Header">
      <div className="header_flex">
        <div className="company_name">
          <img src={redberry} alt="redberry"></img>
        </div>
        <div className="page_numbering">{page}/4</div>
      </div>
      <hr />
    </div>
  );
};

export default Header;
